import { environment } from '@ali/4ever-cangjie';

const MOD = environment.IS_MAC ? 'âŒ˜' : 'Ctrl';

export const BIGGER_SZ_HOT_KEY_TIP = `${MOD}+Shfit+>`;
export const SMALLER_SZ_HOT_KEY_TIP = `${MOD}+Shift+<`;

export const SHORTCUT = {
  bigger: `${MOD}+Shift+>`,
  smaller: `${MOD}+Shift+<`,
};

export default function getTipsByType(type: string, locale: Record<string, any>) {
  const title = type === 'bigger' ? locale.increaseSzButtonTitle : locale.decreaseSzButtonTitle;
  return `${title} ${SHORTCUT[type]}`;
}
